.command-palette {
  width: 500px;
  max-width: 100%;
  position: absolute;
  top: 100px;
  left: 50%;
  -webkit-box-shadow: 2px 2px 12px rgba(0,0,0,0.2);
          box-shadow: 2px 2px 12px rgba(0,0,0,0.2);
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  background-color: #fff;
  font-size: 16px;
}

.command-palette input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  padding: 6px 8px;
  margin: 4px;
  border-radius: 0;
  width: calc(100% - 8px);
  border: none;
  -webkit-box-shadow: 2px 2px 2px inset rgba(0,0,0,0.3);
          box-shadow: 2px 2px 2px inset rgba(0,0,0,0.3);
  border: 1px solid #eee;
}

.command-palette-action {
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 10px 8px 10px 12px;
  border: none;
  width: 100%;
  text-align: left;
  font: inherit;
}

.command-palette input:focus + div .command-palette-action, .command-palette-action:focus {
  background-color: #eee;
}

.command-palette-action .key {
  color: #000;
  background-color: #bdf;
  border-radius: 3px;
  padding: 2px 5px;
  font-size: 14px;
  -ms-flex-item-align: center;
      align-self: center;
}

.command-palette-action .name {
  font-weight: bold;
  min-width: 80px;
  -ms-flex-item-align: center;
      align-self: center;
}

.command-palette-action .description {
  color: #888;
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 0 20px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  font-size: 14px;
  white-space: nowrap;
  -ms-flex-item-align: center;
      align-self: center;
}


